@import '../styles/variables';

.card{
    color: white;
    margin-top: 3rem; 
    text-decoration: none;
    padding-left: 2rem;
    
    .card-body{
        
        position: relative;
        background: black;
        height: 11.5rem;
        border-radius: 1rem;
        padding-top: 0.5px;
        line-height : 10px;
        margin: auto;
        top: -20px;
       
        
        @media screen and (max-width: 500px){
            width: 12rem;
            height: 10.5rem;
            font-size: .8rem;
            
        }
        .add-fav-button{
            margin-bottom: 1px;
            border-radius: 10px;
            ;
        }
    
        .add-fav-button, .add-fav-heart{
            background-color: $background-black;
            border: none;
        }
        .add-fav-title{
            background-color: $background-black;
            color: $white-font;
            border: none;
        }
        .added-fav-button, .added-fav-heart{
            background-color: $background-black;
            border: none;
        }
        .added-fav-title{
            background-color: $background-black;
            color: $white-font;
            border: none;
           
        }
        .card-num-stat{
            display: flex;
            align-items: baseline;
        }
    
    }
    .card-number, .card-specie{
        color:  $yellow-font;
   
    }
    .card-number{
        text-align: justify;
        margin-left: 20px;
        padding-top: 5px;
    }
    .card-title{
        color: $white-font;
        line-height: 1.2rem;
        font-size: 1.5rem;
      
    }
    .card-image{
        border-radius: 50%;
        margin-top: 20px;
        width: 15rem;
    }
    a{
        color: rgb(228, 105, 53);
      
    }
    a:link {
        line-height: 1.5rem;
        text-decoration-line: none;
        font-size: 1rem;
        cursor: pointer;
        
    }
    .card-see-btn{
        padding-top: .8rem;
    }


    .card-close-btn {
        position:relative;
        background-color: $background-black;
        border: 0;
        color: red; 
        top: -185px;
        right: -90px;
        top: -175px;
        right: -75px;
        font-size: .8rem;

    }
        .card-image {
            width: 12rem;
        }

    }