@import '../styles/variables';

.nav {
            
      .nav-links {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            height: 100%;
            text-align: center;
            position: relative;
            z-index: 1;
            padding: 20px;
            cursor: pointer;

                &.active{
                    display: flex;
                    @include flex ( row, nowrap, flex-end, center);
                    @include media(){ flex-direction: column; }
                }
                @include media(){
                    // display: none;
                    flex-direction: column;
                    align-items: flex-start;
                    width: 100%;
                    transform: translateX(-5%);
            }
    }
      a {
            color: white;
            display: inline-flex;
            padding-left: 10%;
            padding-right: 10%;
            font-size: 1.5rem;
            margin: 5px 0;   
    }
      a:link {
            text-decoration-line: none;
            cursor: pointer;   
            
            @include media() {
                display: flex;
                flex-direction: column;
            }
    }
      a:hover{
            color: rgb(228, 105, 53);
    }
      .logout-button {
            font-family: 'Inter', sans-serif;
            background: $background-color-buttons;
            width: 120px;
            height: 2rem;
            color: $white-font;
            font-size: 20px;
            border-radius: 2rem;
            border: none;
            box-shadow: 3px 3px 3px 1px rgb(121, 116, 116);  
           
    }
      .logout-button:hover{
            background: rgb(9, 67, 13);
    }
      .centered {
            display: flex;
            justify-content: center;
            align-items: center;
    }
      .nav-image img{
            width: 500px;
            height: 250px;
    }

    //MENU RESPONSIVE
  
     .nav-links .show-menu{
        transform: translate(0%);
    }
     .nav-links-menu{
        width: 100%;
        height: 100%;
    }
    
    @media screen and (max-width: 500px){
        .nav-links {
            display: flex;
            flex-direction: column;
            height: auto;
            background-color: transparent;
            overflow-x: visible;
            width: 100%;

        }
        .nav-links-menu {
            display: none;
            position: absolute;
            height: 50vh;
            cursor: pointer;
        }
        .nav-links-menu a{
            display: block;
        }
          .nav-links a {
            display: flex;
            margin-bottom: 10px;
            
          }
          .logout-button{
            margin-top: 50px;
          }
          .nav-image img{
            width: 400px;
            height: 200px;
          }
    }
}
