@import '../styles/variables';

.favorites-container{
    width: 30rem;
    display: inline;
    opacity: 0.8;

    .fav-list-img{
        padding: 1rem 1rem;
    }
    .fav-img{
        display: flex;
        border-radius: 1rem;
        
    }
    .fav-title{
        color: rgb(228, 105, 53);
    }
    .fav-id{
        font-family:Georgia, 'Times New Roman', Times, serif;
        font-size: 20px;
        margin: auto;
        font-weight: 700;
    }

    .fav-list{
        display: inline-flex;
        border-radius: 1rem;
        background-color: rgb(230, 171, 229);
        width: 40rem;
        justify-content: space-around;
        color: $black-font;
        padding: 2rem 2rem;
    }
    .fav-list-items{
        background-color: rgb(228, 105, 53);
        color: $white-font;
        font-size: 15px;
        border-radius: 5px;
        width: 15rem;
        height: 10rem;
        display: inline-block;
        margin-top: 1rem;
    }
    .fav-origin h3{
        text-align: center;
    }
    
    h1{
        color: $white-font;
    }
    .goHome-fav{
        font-family: 'Inter', sans-serif;
        background: $background-color-buttons;
        width: 170px;
        height: 30px;
        padding: 0 10px;
        color: $white-font;
        font-size: 20px;
        border-radius: 2rem;
        border: none;
        box-shadow: 3px 3px 3px 1px rgb(121, 116, 116);  
    }
     button{
        // /position: relative;
        background: $background-favorite-card;
        box-shadow: 3px 3px 3px 1px rgb(121, 116, 116);  
        border-radius: .5rem;
        border: none;
        color: $white-font;
        height: 2rem;
    }
    .alive-color, .dead-color, .unknown-color{
        font-family:Georgia, 'Times New Roman', Times, serif;
        color: $white-font;
        font-size: 25px;
        border-radius: 5px;
        width: 15rem;
        display: inline-block;
    }
    .alive-color {
        background-color: rgba(40, 110, 40, 0.96);
    }
    .dead-color {
        background-color: rgba(144, 10, 14, 0.96);
    }
    .unknown-color{
         background-color: rgb(58, 56, 56);   
    }

    @media screen and (max-width: 500px){
        .fav-img{
            display: inline;
            width: 180px;

        }
        .fav-list {
            display: inline-block;
            width: 80%;
        }
        .fav-list-items{
            line-height: .8rem;
        }
   
    }
    @media (min-width: 500px) and (max-width: 850px){
        .fav-img{
            display: inline;
            width: 200px;

        }
        .fav-list {
            display: inline-block;
            width: 80%;
        }
        .fav-list-items{
            line-height: 1.5rem;
        }
    }
}