@import '../styles/variables';


.cards:not(:empty){
    background-color: rgb(176, 248, 176);
    opacity: .8;
    //padding-left: 25px;
    //padding-bottom: 40px;
    padding-right: 25px;
    border-radius: .5rem;
    display: flex;
    flex-flow: wrap;
    float: left;
    padding: '10px';
    align-items: center;
    font-size: 1rem;
    color: white;
    width: 90%;
    margin-left: 30px;
    padding-bottom: 70px;
    justify-content: center;
    justify-content: space-between;
    
    .cards:empty{
      background-color: transparent;
     
    }

    .alive-color {
      color: $green-font;
      padding-left: 20px;
  }

    .dead-color {
      color: $red-font;
      padding-left: 20px;
  }
    .unknown-color{
      color: $white-font;
      padding-left: 20px;
    }



  @media screen and (max-width: 500px){
      flex-direction: column;   
      display: contents;
  }
  @media (min-width: 501px) and (max-width: 654px){
      flex-direction: column;   
  }
  

  @media (min-width: 655px) and (max-width: 800px){
   
    .cards{
      justify-content: space-between;
      display: flex;
      flex-flow: wrap;
}
  }
  }
  